
import {
  defineComponent,
  ref,
  reactive,
  computed,
  onMounted,
  nextTick
} from 'vue'
import { ElMessage, ElMessageBox, ElAutocomplete } from 'element-plus'
import { useRouter } from 'vue-router'
import { useI18n } from 'vue-i18n'
import { ElForm } from 'element-plus'
import { FormModel, LinkItem, Data } from '../detail/type/index'
export default defineComponent({
  name: 'edit',
  components: {
    ElAutocomplete
  },
  setup() {
    const { t } = useI18n()
    const data = ref<any>([])
    const router = useRouter()
    const dialogVisible = ref(false)
    const links = ref<LinkItem[]>([])
    const formModel = ref<FormModel>({ data: [] })
    onMounted(() => {
      console.log(
        router.currentRoute.value.query,
        'router.currentRoute.value.query'
      )
      links.value = loadAll()
      nextTick(() => {
        formModel.value.barcode = '5adgdla'
      })

      if (Object.keys(router.currentRoute.value.query).length !== 0) {
        data.value = [
          {
            name: '王小虎',
            phone: 15251909379,
            countryCode: 'Canada',
            provinceCode: 'BC',
            cityCode: 'ac',
            zipCode: '156711',
            addressOne: '12 esct Floor 2',
            addressTwo: '61 Floor 3',
            isDefault: 1
          },
          {
            name: '张三',
            phone: 15251909378,
            countryCode: 'Canada',
            provinceCode: 'BC',
            cityCode: 'ac',
            zipCode: '156711',
            addressOne: '13 esct Floor 2',
            addressTwo: '2 Floor 3',
            isDefault: 0
          }
        ]
      } else {
        data.value = []
      }
    })
    const loadAll = () => {
      return [
        {
          sku: 'xbz',
          productDescr: 'adcvvaa',
          barcode: '3737004'
        },
        {
          sku: 'swy',
          productDescr: 'adcvvaa',
          barcode: '3737003'
        }
      ]
    }
    const handleSelect = (item: LinkItem) => {
      const params: Data = {}
      params.sku = item.sku
      params.productDescr = item.productDescr
      params.barcode = item.barcode
      formModel.value.data.push(params)
    }
    const formRef = ref<InstanceType<typeof ElForm>>()
    const rules = reactive({
      barcode: [{ required: true, trigger: 'change' }],
      categoryCode: [{ required: true, trigger: 'change' }]
    })
    const cancel = () => {
      formRef.value?.resetFields()
      router.push({ path: '/main/Inventory/kitmanagement/' })
    }
    const querySearch = (queryString: string, cb: any) => {
      const results = queryString
        ? links.value.filter(createFilter(queryString))
        : links.value
      // call callback function to return suggestion objects
      cb(results)
    }
    const createFilter = (queryString: any) => {
      return (restaurant: any) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        )
      }
    }
    // 删除
    const handleRow = (rowData: any) => {
      // sku
      const index = formModel.value.data.findIndex(
        (e: any) => e.sku === rowData.sku
      )
      //   console.log(index, 'index1')

      ElMessageBox.confirm(t('deleteChange'), t('deleteTips'), {
        distinguishCancelAndClose: true,
        confirmButtonText: t('confirm'),
        cancelButtonText: t('cancel'),
        customClass: 'message-box',
        center: true
      })
        .then(() => {
          formModel.value.data.splice(index, 1)
          ElMessage({
            type: 'success',
            message: t('deleteSuccessful')
          })
        })
        .catch((action: any) => {
          ElMessage({
            type: 'info',
            message: action === 'cancel' ? t('deleteFailed') : t('canceled')
          })
        })
    }
    const submitForm = () => {
      formRef.value?.validate((valid) => {
        console.log(valid, 'vaild')
        if (valid) {
          router.push({ path: '/main/Inventory/kitmanagement/' })
        }
      })
    }
    return {
      formModel,
      rules,
      cancel,
      submitForm,
      formRef,
      t,
      data,
      dialogVisible,
      ElMessage,
      handleRow,
      querySearch,
      handleSelect,
      loadAll
    }
  }
})
